* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
  outline-offset: 20px;
  transition: 0.2s all ease;
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  border-radius: 3px;
  outline: 4px solid #cbbb9f;
  outline-offset: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #cbbb9f;
}

:root {
  scrollbar-color: #cbbb9f transparent;
  scrollbar-width: thin;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

main {
  margin-bottom: auto;
}

body {
  font-size: 1rem;
  line-height: 1.25rem;
  background-color: white;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  overflow: hidden;
}

.container {
  max-width: 76.875rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.logo__img {
  width: 9.6875rem;
  height: 5.25rem;
  transition: all 0.4s ease-in-out;
}
.logo__img:hover {
  opacity: 0.8;
}

.btn {
  position: relative;
  padding: 1.25rem 1.5625rem;
  cursor: pointer;
}
.btn .btn__text {
  position: relative;
  z-index: 2;
  font-size: 1.125rem;
  line-height: 1.375rem;
  color: white;
  text-transform: uppercase;
  transition: all 0.4s ease-in-out;
}
.btn--fill::before, .btn--fill::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 0.5rem;
  transition: all 0.4s ease-in-out;
}
.btn--fill::before {
  z-index: 1;
  background-color: #887569;
}
.btn--fill::after {
  border: 1px solid #887569;
  opacity: 0;
  transform: scale(1.2, 1.2);
}
.btn--fill:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.btn--fill:hover::after {
  opacity: 1;
  transform: scale(1, 1);
}
.btn--fill:hover .btn__text {
  color: #887569;
}
.btn--fill:active::after {
  border-color: #958276;
}
.btn--transparent .btn__text {
  color: #887569;
}
.btn--transparent::before, .btn--transparent::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 0.5rem;
  transition: all 0.4s ease-in-out;
}
.btn--transparent::after {
  z-index: 1;
  background-color: #887569;
  opacity: 0;
  transform: scale(1.2, 1.2);
}
.btn--transparent::before {
  border: 0.0625rem solid #887569;
  opacity: 1;
}
.btn--transparent:hover .btn__text {
  color: white;
}
.btn--transparent:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.btn--transparent:hover::after {
  opacity: 1;
  transform: scale(1, 1);
}
.btn--transparent:active::after {
  background-color: #958276;
}
.btn--arrow {
  display: inline-block;
  padding: 1.3125rem 2.8125rem;
  text-align: center;
  border: 0.0625rem solid #887569;
  border-radius: 0.5rem;
}
.btn--arrow .btn__text {
  position: relative;
  padding-right: 2.0625rem;
  color: #887569;
}
.btn--arrow .btn__text::after {
  position: absolute;
  top: 50%;
  right: 0%;
  width: 0.875rem;
  height: 0.5rem;
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 14 8'%3e%3cpath fill='%23887569' d='M0 4c0 .245.22.444.492.444h11.844L9.233 7.245a.421.421 0 0 0-.143.313c0 .113.049.227.143.312a.53.53 0 0 0 .695 0l3.935-3.557a.427.427 0 0 0 0-.628L9.925.13a.53.53 0 0 0-.695 0 .415.415 0 0 0 0 .628l3.1 2.8H.49C.217 3.556 0 3.755 0 4Z'/%3e%3c/svg%3e ");
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}
.btn--arrow:hover .btn__text::after {
  right: -5%;
}

@media (max-width: 500px) {
  .btn {
    padding: 1rem 1.25rem;
  }
  .btn__text {
    font-size: 0.75rem;
    line-height: 0.9375rem;
  }
}
.input {
  width: 100%;
  padding: 1.3125rem 1.8125rem;
  color: #9f9f9f;
  background-color: white;
  border: 0.0625rem solid #959595;
  border-radius: 0.5rem;
}

@media (max-width: 500px) {
